import operate from '@/common/operate.js'

export default class Request {
    http(param) {
        // 
        var url = param.url,
            method = param.method,
            header = {},
            data = param.data || {},
            token = param.token || "",
            hideLoading = param.hideLoading || false;

        //
        var requestUrl = operate.api + url;
       //
       // var requestUrl = operate.api() + url;

        //:GETPOST(POST
        // header: {'content-type' : "application/x-www-form-urlencoded"},)
        if (method) {
            method = method.toUpperCase(); //
            if (method == "POST") {
                header = {
                    'content-type': "application/x-www-form-urlencoded"
                };
            } else {
                header = {
                    'content-type': "application/json"
                };
            }
        }

        //
        if (!hideLoading) {
            uni.showLoading({
                title: '...'
            });
        }

        // promise
        return new Promise((resolve, reject) => {
            // 
            uni.request({
                url: requestUrl,
                data: data,
                method: method,
                header: header,
                success: (res) => {
                    //  api 
                    if (res.statusCode && res.statusCode != 200) {
                        uni.showToast({
                            title: "api" + res.errMsg,
                            icon: 'none'
                        });
                        return;
                    }
                    // 
                    resolve(res.data)
                },
                //
                fail: (e) => {
					console.log(e.errMsg)
                    uni.showToast({
                        title: "" + e.errMsg,
                        icon: 'none'
                    });
                    resolve(e.data);
                },
                //
                complete() {
                    //
                    if (!hideLoading) {
                        uni.hideLoading();
                    }
                    resolve();
                    return;
                }
            })
        })
    }
}
